.list__item {
    display: flex;
    align-items: center;
    margin-bottom: 1.5rem;
}
.icon {
    width: 2.4rem;
    height: 2.4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
}
.icon.check {
    background-color: #00A365;
}
.icon.cancel {
    background-color: #EB5757;
}
span.icon {
    color: #fff;
    font-size: 0.8rem;
}
.list__item_text {
    width: calc(95.9% - 2.4rem);
    margin-left: 4.1%;
    font-size: 1.6rem;
    letter-spacing: 0.01em;
    color: #283239;
}