@import url('./assets/fonts/icomoon/style.css');

@font-face {
  src: url('./assets/fonts/Shopify Sans Web Bold Regular.ttf'),
       url('./assets/fonts/Shopify Sans Web Bold Regular.woff2');
  font-family: 'Shopify Sans Web Bold';
}
@font-face {
  src: url('./assets/fonts/Shopify Sans Web Medium Regular.ttf'),
       url('./assets/fonts/Shopify Sans Web Medium Regular.woff2');
  font-family: 'Shopify Sans Web Medium';
}
@font-face {
  src: url('./assets/fonts/Shopify Sans Web Regular Regular.ttf'),
       url('./assets/fonts/Shopify Sans Web Regular Regular.woff2');
  font-family: 'Shopify Sans Web Regular';
}

/*Reset*/
* {
    padding: 0;
    margin: 0;
    line-height: 1;
    box-sizing: border-box;
}
a, img, button, input {
    display: block;
}
a, img, button {
    cursor: pointer;
}
button {
    background-color: transparent;
    border: none;
}
a {
    text-decoration: none;
}
button:focus, input:focus {
    outline: none;
}
ul {
    list-style-type: none;
}
body {
    overflow-x: hidden;
}
/*Main settings*/
.App {
    position: relative;
    width: 100%;
}
html {
    font-size: 10px;
    font-family: 'Shopify Sans Web Regular';
}
.wrapper {
    width: 100%;
    background-size: cover;
    background-position: center;
}
.section{
    width: 100%;
}
.container{
    width: 1170px;
    margin: auto;
}
.row {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.section__name {
    margin-bottom: 1.6rem;
    text-transform: uppercase;
    text-align: center;
    font-family: 'Shopify Sans Web Bold';
    font-size: 1.6rem;
    line-height: 4.0rem;
    color: #FFBE0A;
}
.section__title {
    text-align: center;
    font-family: 'Shopify Sans Web Bold';
    font-size: 4.8rem;
    line-height: 5.6rem;
    letter-spacing: 0.01em;
    color: #283239;
}
@media screen and (max-width: 1280px) {
    html {
        font-size: 9px;
    }
    .container {
        width: 960px;
    }
}
@media screen and (max-width: 1100px) {
    .container {
        width: 860px;
    }
}
@media screen and (max-width: 991px) {
    .container {
        width: 720px;
    }
}
@media screen and (max-width: 767px) {
    .container {
        width: 540px;
    }
}
@media screen and (max-width: 575px) {
    .container {
        width: 350px;
    }
}
@media screen and (max-width: 374px) {
    .container {
        width: 310px;
    }
}