.s_1 .section__title {
    margin-bottom: 8.8rem;
}
.s_1 .row {
    align-items:stretch;
}
.left__block {
    width: 50%;
    position: relative;
}
.hex__bg {
    position: absolute;
    width: 63.25%;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, 30%);
}
.hex__group__bg {
    position: absolute;
}
.hex__group_bg {
    top: 0;
    left: 63.76%;
    width: 26%;
    transform: translateY(-50%);
}
.content__group {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 102.56%;
    height: 93.6%;
}
.content__group.block__1 {
    background-color: #008552;
    box-shadow: 0px 3.2rem 3.6rem rgba(129, 151, 167, 0.24);
}
.content__group.block__1 .block__2 {
    width: 88.333%;
    background-color: #283239;
    padding: 2.64% 5.2%;
}
.left__block .content__bar {
    margin-bottom: 5.85%;
    border-radius: 0.2rem;
    background: #FFFBF0;
    opacity: 0.2;

}
.left__block .content__bar.active {
    width:4%;
    background-color: #FFFBF0;
    opacity: 1;
}
.left__block .content__bar, .left__block .content__bar.mid{
    width: 2.7%;
}

/*Right Block*/
.right__block {
    width: 50%;
    padding-left: 8%;
}
.block__title {
    width: 86%;
    font-size: 2.4rem;
    line-height: 3.6rem;
    letter-spacing: 0.01em;
    color: #283239;
    margin-bottom: 2.4rem;
}
.block__text {
    width: 87.58%;
    font-size: 1.6rem;
    line-height: 187%;
    letter-spacing: 0.01em;
    color: #526675;
    margin-bottom: 2.4rem;
}

/*C_2*/
.c_2 {
    margin-top: 18.2%;
}
.c_2 .left__block{
    order: 2;
}
.c_2 .hex__bg {
    width: 55%;
    left: 94.7%;
    transform: translate(-100%, 20%);
}
.c_2 .hex__group_bg {
    top: 55.31%;
    left: 23.07%;
}
.c_2 .block__title {
    width: 100%;
}
.c_2 .block__text {
    width: 100%;
}
.ellipse__bg {
    width: 21.54%;
    position: absolute;
    top: 0;
    left: 90.08%;
    transform: translate(-100%, -50%);
}
.c_2 .ab__card {
    position: absolute;
    top: -10.8%;
    left: 37.1%;
    width: 46.15%;
    padding: 0;
}
.ab__card__wrapper {
    position: relative;
    width: 100%;
    padding: 12.98% 7.41% 16%;
}
.c_2 .ab__card_header {
    width: 100%;
    margin-bottom: 5.65%;
    position: relative;
    padding-top: 58.26%;
    background-color: #178A9E;
    box-shadow: 0px 3.2rem 3.6rem rgba(129, 151, 167, 0.34);
    border-radius: 1.6rem;
}
.c_2 .ab__card_header img {
    position: absolute;
    width: 73.04%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.c_2 .ab__card_body {
    padding: 0 7.83%;
}
.card__body_text {
    font-family: 'Shopify Sans Web Regular';
    font-size: 1.6rem;
    line-height: 250%;
    color: #283239;
    margin-bottom: 3.9%;
}
.card__body__text_bar {
    height: 0.4rem;
    background: #8197A7;
    opacity: 0.5;
    border-radius: 0.1rem;
    margin-bottom: 3.7%;
}
.card__body__text_bar:nth-child(2) {
     width: 55.67%;
 }
.card__body__text_bar:nth-child(3) {
    width: 30.93%;
}
.card__body__text_bar:nth-child(4) {
    width: 51.55%;
}
.card__body__text_bar:nth-child(5) {
    width: 18.56%;
}
.c2_btn{
    margin: 2.2rem 0 0;
}
.card__partner__block {
    position: absolute;
    z-index: 2;
}
.card__partner__block .block{
    position: relative;
    width: 100%;
    padding-top: 100%;
}
.card__partner__block img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.card__partner__block.p_5 {
    width: 36.3%;
    top: 28.92%;
    left: 0;
    transform: translate(-50%, 0);
}
.card__partner__block.p_5 .block {
    border-radius: 50%;
    background: #FFFFFF;
    box-shadow: 0px 3.2rem 3.6rem rgba(129, 151, 167, 0.24);
}
.card__partner__block.p_5 img {
    width: 28.5%;
}
.card__partner__block.p_7 {
    width: 41.48%;
    top: 51.35%;
    right: 0;
    transform: translate(66.96%, 0);
}
.card__partner__block.p_7 .block {
    background: #8197A7;
    box-shadow: 0px 4.8rem 3.6rem rgba(129, 151, 167, 0.38);
    border-radius: 1.6rem;
}
.card__partner__block.p_7 img {
    width: 59.82%;
}
.c2_check_btn {
    position: absolute;
    bottom: 0;
    transform: translateY(40%)
}
button.c2_check_btn {
    width: 96.67%;
}

/*C_3*/
.c_3 {
    margin-top: 18.3%;
}
.c_3 .ab__card {
    width: 37.94%;
    padding: 3.76% 3.76% 4.77% 5.13%;
    background: #FFFFFF;
    box-shadow: 0px 3.2rem 3.6rem rgba(129, 151, 167, 0.24);
    border-radius: 1.6rem;
}
.c_3 .ab__card:nth-child(3) {
    top: 0;
    left: 50%;
    transform: translate(-50%, -11%);
}
.c_3 .ab__card:nth-child(4) {
    top: 100%;
    right: 0%;
    transform: translate(-30%, -70%);
}
.c_3 .ab__card__icon {
    width: 4.6rem;
    height: 4.6rem;
    margin-bottom: 1.7rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
}
.c_3 .ab__card:nth-child(3) .ab__card__icon {
    background-color: #EB5757;
    font-size: 3.1rem;
}
.c_3 .ab__card:nth-child(4) .ab__card__icon {
    background-color: #008F5F;
    font-size: 1.6rem;
}
.c_3 .ab__card__title {
    width: 75%;
    font-size: 1.6rem;
    line-height: 125%;
    letter-spacing: 0.01em;
    color: #283239;
}
.c_3 .ab__card:nth-child(3) .ab__card__title {
    margin-bottom: 2.6rem;
}
.c_3 .ab__card:nth-child(4) .ab__card__title {
    margin-bottom: 0.8rem;
}
.c_3 .text__bar {
    margin-bottom: 0.8rem;
}
.c_3 .ab__card:nth-child(3) .text__bar:first-child{
    width: 63.53%;
}
.c_3 .ab__card:nth-child(3) .text__bar:nth-child(2){
    width: 35.29%;
}
.c_3 .ab__card:nth-child(3) .text__bar:nth-child(3){
    width: 58.82%;
}
.c_3 .ab__card:nth-child(3) .text__bar:nth-child(4){
    width: 21.2%;
    margin-bottom: 0;
}
.c_3 .ab__card:nth-child(4) .text__bar:first-child{
    width: 63.53%;
}
.c_3 .ab__card:nth-child(4) .text__bar:nth-child(2){
    width: 35.29%;
    margin-bottom: 2.2rem;
}
@media screen and (max-width: 1280px) {
    .content__group {
        width: 114.56%;
        height: 95%;
    }
    button.c2_check_btn {
        height: 4.6rem;
        font-size: 12px;
        line-height: 18px;
    }
    .c_2 .hex__bg {
        width: 55%;
        left: 94.7%;
        transform: translate(-100%, 8%);
    }
}
@media screen and (max-width: 1100px) {
    .content__group {
        width: 115.56%;
    }
    .right__block {
        padding-left: 4%;
    }
    .block__title {
        width: 100%;
        margin-bottom: 1.6rem;
    }
    .block__text {
        width: 100%;
        line-height: 147%;
        margin-bottom: 1.6rem;
    }
}
