    .s_1 .row {
        flex-direction: column;
    }
    .left__block, .right__block {
        width: 100%;
    }
    .left__block {
        padding-top: 66.333%;
    }
    .content__group {
        width: 100%;
    }
    .right__block {
        padding-left: 0;
        margin-top: 30%;
    }
