    }
    button.c2_check_btn {
        width: 261px;
        height: 56px;
        border-radius: 28px;
        font-size: 14px;
        line-height: 25px;
    }
    .c_2 .hex__bg {
        width: 55%;
        left: 94.7%;
        transform: translate(-100%, 0%);
    }
    .c2_btn {
        height: 4.8rem;
        border-radius: 2.4rem;
        font-size: 1.6rem;
        width: 75%;
    }
    .c_3 .ab__card {
        padding: 5.76% 3.76% 6.77% 5.13%;
    }
    .c_3 .ab__card:nth-child(3) {
        transform: translate(-50%, 0%);
    }
    .c_3 .ab__card:nth-child(4) {
        transform: translate(-30%, -90%);
    }
    .c_3 .ab__card__title {
        width: 100%;
        font-size: 2.6rem;
    }
    .c_3 .text__bar {
        height: 6px;
        border-radius: 4px;
        margin-bottom: 1.6rem;
    }
}
@media screen and (max-width: 767px) {
    .left__block {
        padding-top: 72.333%;
    }
    .c_3 .ab__card__title {
        width: 100%;
        font-size: 2rem;
    }
}
